@import url(https://fonts.googleapis.com/css?family=Open+Sans:300,400,700);
@import url(https://fonts.googleapis.com/icon?family=Material+Icons);
/* GRID */

.row {
  margin: 0;
  padding: 0;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  width: 100%;
}

.row-middle {
  -webkit-align-items: center;
          align-items: center;
}

.col {
  -webkit-flex-grow: 1;
          flex-grow: 1;
  -webkit-flex-basis: 0;
          flex-basis: 0;
  max-width: 100%;
}

.col-start {
  -webkit-justify-content: flex-start;
          justify-content: flex-start;
  text-align: left;
}

.col-center {
  -webkit-justify-content: center;
          justify-content: center;
  text-align: center;
}

.col-end {
  -webkit-justify-content: flex-end;
          justify-content: flex-end;
  text-align: right;
}


/* Calendar */

.calendar {
  display: block;
  position: relative;
  width: 100%;
  background: var(--neutral-color);
  border: 1px solid var(--border-color);
}

.calendar .header {
  text-transform: uppercase;
  font-weight: 700;
  font-size: 115%;
  padding: 1.5em 0;
  border-bottom: 1px solid var(--border-color);
}

.calendar .header .icon, 
span.modal .icon.action,
span[class*="-modal"] .icon.action{
  cursor: pointer;
  transition: .15s ease-out;
}

.calendar .header .icon:hover,
span.modal .icon.action:hover,
span[class*="-modal"] .icon.action:hover{
  -webkit-transform: scale(1.25);
          transform: scale(1.25);
  transition: .25s ease-out;
  color: var(--main-color);
}

.calendar .header .icon:first-of-type {
  margin-left: 1em;
}

.calendar .header .icon:last-of-type {
  margin-right: 1em;
}

.calendar .days {
  text-transform: uppercase;
  font-weight: 400;
  color: var(--text-color-light);
  font-size: 70%;
  padding: .75em 0;
  border-bottom: 1px solid var(--border-color);
}

.calendar .body .cell {
  position: relative;
  height: 5em;
  border-right: 1px solid var(--border-color);
  cursor: pointer;
  overflow: hidden;
  background: var(--neutral-color);
  transition: 0.25s ease-out;
}

.calendar .body .cell:hover {
  background: var(--bg-color);
  transition: 0.5s ease-out;
}

.calendar .body .selected {
  border-left: 10px solid transparent;
  border-image: linear-gradient(45deg, #1a8fff 0%,#53cbf1 40%);
  border-image-slice: 1;
}

.calendar .body .row {
  border-bottom: 1px solid var(--border-color);
}

.calendar .body .row:last-child {
  border-bottom: none;
}

.calendar .body .cell:last-child {
  border-right: none;
}

.calendar .body .cell .number {
  position: absolute;
  font-size: 82.5%;
  line-height: 1;
  top: .75em;
  right: .75em;
  font-weight: 700;
}

.calendar .body .disabled {
  color: var(--text-color-light);
  pointer-events: none;
}

.calendar .body .cell .bg {
  font-weight: 700;
  line-height: 1;
  color: var(--main-color);
  opacity: 0;
  font-size: 8em;
  position: absolute;
  top: -.2em;
  right: -.05em;
  transition: .25s ease-out;
  letter-spacing: -.07em;
}

.calendar .body .cell:hover .bg, .calendar .body .selected .bg  {
  opacity: 0.05;
  transition: .5s ease-in;
}

.calendar .body .col {
  -webkit-flex-grow: 0;
          flex-grow: 0;
  -webkit-flex-basis: calc(100%/7);
          flex-basis: calc(100%/7);
  width: calc(100%/7);
}

span.modal-container {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
}

span.modal {
  position: relative;
  height: 250px;
  width: 320px;
  background-color:var(--bg-color);
  border-radius: 10px;
  box-shadow: 1px 1px 20px rgba(0,0,0,.15);
  padding: 40px;
}
span[class*="-modal -interactive"] {
  position: relative;
  top: 100px;
  height: 250px;
  width: 320px;
  background-color:var(--bg-color);
  border-radius: 10px;
  box-shadow: 1px 1px 20px rgba(0,0,0,.15);
  padding: 40px;
  z-index: 2;
}

span[class*="-modal -flexable"]{
  position: relative;
  background-color:var(--bg-color);
  border-radius: 10px;
  box-shadow: 1px 1px 20px rgba(0,0,0,.15);
  padding: 40px 10px;
  min-height: 175px;
  min-width: 225px;
}

/* FONT IMPORTS */

.icon {
  font-family: 'Material Icons', serif;
  font-style: normal;
  display: inline-block;
  vertical-align: middle;
  line-height: 1;
  text-transform: none;
  letter-spacing: normal;
  word-wrap: normal;
  white-space: nowrap;
  direction: ltr;

  -webkit-font-smoothing: antialiased;
  text-rendering: optimizeLegibility;
  -moz-osx-font-smoothing: grayscale;
  -webkit-font-feature-settings: 'liga';
          font-feature-settings: 'liga';
}


/* VARIABLES */

:root {
  --main-color: #1a8fff;
  --text-color: #777;
  --text-color-light: #ccc;
  --border-color: #eee;
  --bg-color: #f9f9f9;
  --neutral-color: #fff;
}


/* GENERAL */

html {
  box-sizing: border-box;
  position: relative;
  min-height: 100%;
  padding-bottom: calc(var(--footer-height) + 50px);
  font-size: 16px;
  background-color: var(--off-white);
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

body {
    font-family: 'Open Sans', 'Helvetica Neue', 'Helvetica', 'Arial', sans-serif;
    font-size: 1em;
    font-weight: 300;
    color: #777;
    color: var(--text-color);
    background: #f9f9f9;
    background: var(--bg-color);
    position: relative;
}

header {
  display: block;
  width: 100%;
  padding: 1.75em 0;
  border-bottom: 1px solid #eee;
  border-bottom: 1px solid var(--border-color);
  background: #fff;
  background: var(--neutral-color);
}

header #logo {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: 175%;
  text-align: center;
  color: #1a8fff;
  color: var(--main-color);
  line-height: 1;
}

header #logo .icon {
  font-size: 115%;
  padding-right: .25em;
}

main {
  display: block;
  margin: 0 auto;
  margin-top: 1em;
  max-width: 50em;
}

h3, p {
  word-wrap: break-word;
}

h3, p[class*="-sm"]{
  font-size: .75rem
}

span[class*="-scrollY -sm"]{
  display: block;
  overflow-y: scroll;
  height: 125px;
}

input.input {
  height: 30px;
  width: 100%;
  border-top: none;
  border-left: none;
  border-right: none;
  border-bottom: 1px solid rgba(0,0,0,.3);
  outline: none;
  background-color: transparent;
}

button:hover {
  cursor: pointer;
}

button.button,
button.submit-button {
  border: none;
  background-color: #1a8fff;
  outline: none;
  border-radius: 4px;
  width: 100%;
  height: 30px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
}

button.button > span,
button.submit-button > span {
  color:#f9f9f9;
  color:var(--bg-color);
}

input.input.error {
  border-top: none;
  border-left: none;
  border-right: none;
  border-bottom: 1px solid red;
  outline: none;
  background-color: transparent;
}

textarea.textarea {
  resize: none;
  border: none;
  outline: none;
  width: 100%;
  height: 100%;
  border-radius: 5px;
}








